import React, { Component } from 'react';
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <br/><br/><br/>
        <Hello/>
      </div>
    );
  }
}

class Hello extends Component {
  render(){
    const hello = this.displayHello();

    return(
      <div>
        <h2>{hello}</h2>
      </div>
    );
  }
}

displayHello = () => {
  return 'Hello World...!';
}




export default App;
